<template>
<div class="dy-cascader"
			v-outerclick="closeOpts">
	<input type="text" readonly autocomplete="off" 
					v-model="cascader"
					@click="showDropdown">
	<span class="remove"
				v-show="!!cascader"
				@click="removeChoice">&#10006;</span>
	<transition	name="demo">
	<div class="dropdown"
				v-show="isShowCascader">
		<tree :tree-data="treeData"
					@get-choice="getChoice"></tree>
	</div>
	</transition>
</div>
</template>
<script>
import $ from 'jquery'
import tree from './tree.vue'
export default {
	props: {
		treeData: {
			type: Array,
			required: true
		},
		defaultValue: {
			type: Array,
			default: function () {
				return [];
			}
		}
	},
	data () {
		return {
			count: 0,
			flag: false,
			choice: this.defaultValue,
			isShowCascader: false
		}
	},
	methods: {
		getChoice (obj) {
			this.flag = true;
			this.getActive(this.treeData, obj);
		},
		/*获取选中项目的路径,从第一层开始往下找*/
		getActive (data, obj) {
			if (this.flag) {
				this.choice = [];
				this.flag = false;
			}
			var res = data.find(function (el) {
				return el.active;
			});
			this.choice.push(res.parent);

			res.parent === obj.parent ? this.$emit('get-result', this.choice) : this.getActive(res.children, obj);
		},
		/*控制下拉框显隐*/
		showDropdown () {
			this.isShowCascader = true;
		},
		/*为数据加入选中标识变量*/
		addKey (data) {
			var self = this;

			data.forEach(function (el, i) {
				var bool = el.parent === self.choice[self.count] ? true : false; 
				el = Object.assign({}, el, {'active': bool});
				data.splice(i, 1, el);

				bool && self.count++;
				el.children && el.children.length && self.addKey(el.children);

			});

		},
		/*清除所有选中*/
		removeChoice () {
			this.choice = [];
			this.resetList(this.treeData);
			this.$emit('get-result', []);
		},
		/*把项目的标识数据重置*/
		resetList (data) {
			var self = this;
			data.forEach(function (el) {
				el.active = false;
				el.children && el.children.length && self.resetList(el.children);
			});
		},
		closeOpts () {
			this.isShowCascader = false;
		}
	},
	mounted () {
		var self = this;

		self.treeData && self.addKey(self.treeData);

		// $('html, body').click(function(e) {
		// 	self.isShowCascader = false;
		// });
	},
	computed: {
		cascader: {
			get: function () {
				return this.choice.join('/');
			}
		}
	},
	components: {
		tree
	}
}
</script>
<style scoped>
.dy-cascader{position: relative; width: 100%;}
.remove{ position: absolute;right: 7px; top: 7px; font-size: 12px; background-color: #e7e7e7; color: #7e7e7e; padding: 3px 6px;  border-radius: 50%; cursor: pointer; visibility: hidden; opacity: 0;
	transition: all .5s cubic-bezier(0.28, -0.04, 0.42, 1.5);
}
.dy-cascader:hover .remove{visibility: visible; opacity: 1;}
.demo-enter-active, .demo-leave-active {
  transition: all .4s ease;
  transform: translateY(0);
  opacity: 1;
}
.demo-enter, .demo-leave-active {
  transform: translateY(-10px);
  opacity: 0;
}
</style>


