import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
import home from '@/views/home'
// import sort from '@/views/sort'
// import foo from '@/views/foo'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/sort',
      name: 'sort',
      component: resolve => require(['../views/sort.vue'], resolve)
    },
    {
      path: '/filter',
      name: 'filter',
      component: resolve => require(['../views/filter.vue'], resolve)
    },
    {
      path: '*',
      redirect: '/home'
    }
  ]
})



